* {
  box-sizing: border-box;
}

.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
}

body {
  margin: 0;
  background-color: #d3d3d3;
  letter-spacing: .2em;
}

ul,
li {
  list-style: none;
  margin: 0;
  padding: 0;
  font-size: 5vw;
}

pre {
  line-height: 1.5;
  background-color: #00008b;
  color: #fff;
  padding: 1em;
  white-space: pre-wrap;
  letter-spacing: 0;
}

button {
  padding: .5em 1em;
  font-size: clamp(16px,3vw,24px);
}

li+li {
  margin-top: 10vh;
}

a {
  color: #000;
  text-decoration: none;
  display: block;
}

h1 {
  font-size: clamp(20px,5vw,40px);
  position: relative;
  display: inline-block;
  margin: 0;
  white-space: nowrap;
  letter-spacing: .1em;
}

p {
  font-size: clamp(16px,3vw,20px);
  margin-top: 50vh;
}

.bold {
  font-weight: bold;
}

.text-cover {
  background-color: #f2ef5b;
  display: inline-block;
  margin-top: 0;
}

main {
  position: relative;
  margin-top: 100px;
  padding: 20px;
}

main li {
  font-size: 3vw;
}

footer {
  text-align: center;
}

.header {
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 1;
}

.header-inner {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  padding: 20px;
  font-size: 2vw;
}

.drawer {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 2;
  width: 100%;
  min-height: 100vh;
  min-height: calc(var(--vh, 1vh)*100);
  background-color: #f2ef5b;
  display: flex;
  align-items: center;
  justify-content: center;
}

.drawer[aria-expanded=true] {
  overflow-y: hidden;
}

.close-drawer-btn {
  position: absolute;
  top: 20px;
  right: 20px;
}

.drawer-nav-item {
  overflow: hidden;
}

.drawer-nav-item a {
  display: block;
}